body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  background-image: url(/static/media/kembawalker-fivea-bgkd.cf6290f9.jpg);
  height: 1000px;
  background-size: cover;
  background-repeat: no-repeat;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
header {
  padding: 12px;
}

@-webkit-keyframes TitleSqueeze {
  40% {-webkit-transform: scale(1.5, 0.8);transform: scale(1.5, 0.8);}
  70% {-webkit-transform: scale(0.8, 1.2);transform: scale(0.8, 1.2);}
  85% {-webkit-transform: scale(1.1, 0.9);transform: scale(1.1, 0.9);}
}

@keyframes TitleSqueeze {
  40% {-webkit-transform: scale(1.5, 0.8);transform: scale(1.5, 0.8);}
  70% {-webkit-transform: scale(0.8, 1.2);transform: scale(0.8, 1.2);}
  85% {-webkit-transform: scale(1.1, 0.9);transform: scale(1.1, 0.9);}
}

a {
  color: #00c0ce;
}

.title {
  font-size: 24px;
  font-style: oblique;
  text-align: center;
  display: inline-block;
  -webkit-transform: rotate(-2deg);
          transform: rotate(-2deg);
  -webkit-animation: TitleSqueeze 2s ease 2s;
          animation: TitleSqueeze 2s ease 2s;
}

.bordered {
  border: 5px solid;
  border-radius: 8px;
  padding: 4px;
  width: 28px;
  display: inline-block;
  -webkit-transform: skew(10deg);
          transform: skew(10deg);
}

.link {
  display: inline-flex;
  position: absolute;
  right: 32px;
  top: 36px;
  align-items: flex-end;
}

